// 버튼 기본 디자인 설정
@mixin btn-size($w: 70px, $h: 40px, $p: 0 20px, $fz: 15px) {
  position: relative;
  display: inline-block;
  min-width: $w;
  height: $h;
  line-height: $h;
  font-size: $fz;
  text-align: center;
  vertical-align: middle;
  padding: $p;

  input {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%; }

  img, .fa, .icon {
    font-size: ceil(($h - $fz) / 2 * 1.5);
    vertical-align: middle;

    &.before {
      padding-right: 5px; }

    &.after {
      padding-left: 5px; } } }

@mixin btn-style($color: #333, $bd: #585858, $bg: #fff, $bd-hover: #3466a2) {
  font-weight: 500;
  color: $color;
  border: 2px solid $bd;
  background: $bg;

  .fa {
    color: $color; }

  &:hover, &:focus {
    border-color: $bd-hover;
    transition: all .3s ease; } }
