@import 'uikit/jiy-framework';

.main-wrap {
  min-width: 1280px;
  height: 100%;
  @extend %clearfix;
}

.main-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-width: 1280px;
  padding: 40px 40px 0;
  z-index: 20;
  h1 {
    float: left;
  }
  .btn-gnb {
    display: block;
    width: 60px;
    height: 25px;
    position: relative;
    z-index: 30;
    float: right;
    span {
      display: block;
      width: 35px;
      height: 2px;
      background-color: #fff;
      -webkit-transform: rotate(-43deg);
      -moz-transform: rotate(-43deg);
      -ms-transform: rotate(-43deg);
      -o-transform: rotate(-43deg);
      transform: rotate(-43deg);
      position: absolute;
      top: 11px;
      left: -3px;
      -webkit-transition-duration: .5s;
      -moz-transition-duration: .5s;
      -o-transition-duration: .5s;
      transition-duration: .5s;
      &:nth-child(2) {
        left: 12px;
      }
      &:nth-child(3) {
        left: 27px;
      }
    }
    &:hover span {
      &:nth-child(1) {
        top: 0;
        left: 12px;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
      }
      &:nth-child(2) {
        top: 11px;
        left: 12px;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
      }
      &:nth-child(3) {
        top: 23px;
        left: 12px;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
      }
    }
    &.active span {
      background-color: #5ec6c3;
      &:nth-child(1) {
        left: 13px;
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
      }
      &:nth-child(2) {
        opacity: 0;
      }
      &:nth-child(3) {
        left: 13px;
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
      }
    }
    &.active:hover span {
      background-color: #5ec6c3;
      &:nth-child(1) {
        top: 11px;
        left: 13px;
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
      }
      &:nth-child(2) {
        opacity: 0;
      }
      &:nth-child(3) {
        top: 11px;
        left: 13px;
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
      }
    }
  }
  .gnb-wrap {
    width: 736px;
    height: 100%;
    background-color: #6739e3;
    position: fixed;
    top: 0;
    right: 0;
    display: none;
    .gnb {
      position: absolute;
      top: 50%;
      left: 60px;
      font-family: $font-family-ko;
      color: #fff;
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      -o-transform: translateY(-50%);
      transform: translateY(-50%);
      &>ul>li {
        margin-bottom: 65px;
        &:first-child {
          padding-left: 5px;
        }
        &>a {
          font-size: 60px;
          font-size: rem(60px);
          font-weight: 700;
          opacity: .5;
          &:hover {
            opacity: 1;
          }
        }
      }
      .gnb-util {
        margin: 10px 0 0 8px;
        font-size: 16px;
        font-size: rem(16px);
        span {
          position: relative;
          font-weight: 500;
          float: left;
          &:after {
            content: '';
            position: absolute;
            top: 9px;
            right: -30px;
            display: block;
            width: 20px;
            height: 2px;
            background-color: #fff;
          }
        }
        &>ul {
          margin-left: 40px;
          float: left;
          &>li {
            float: left;
            margin-right: 20px;
            &:last-child {
              margin-right: 0;
            }
            &>a {
              font-weight: 400;
              opacity: .7;
              &:hover {
                opacity: 1;
              }
              &.eng-util {
                font-family: 'akrobatsemibold';
              }
            }
          }
        }
      }
    }
  }
}

.video-wrap {
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: block;
  top: 0;
  left: 0;
  z-index: 10;
  video {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    min-width: 1280px;
    width: 100%;
    height: 100vh;
    object-fit: cover;
  }
  .text-wrap {
    position: relative;
    min-width: 1280px;
    height: 100vh;
    .slogan {
      @include wh-center ($position: absolute, $h-percent: 50%, $w-percent: 50%);
      p {
        font-family: $font-family-ko;
        font-size: 30px;
        font-size: rem(30px);
        line-height: 85px;
        color: #fff;
        margin: 0;
        opacity: 0;
        &[class^="slogan-text-"] {
          font-weight: 250;
          &:nth-of-type(even) {
            font-weight: 500;
          }
          span {
            &:last-child {
              margin-right: 0;
            }
          }
        }
        &.slogan-text-1 {
          span {
            margin-right: 6.13em;
          }
        }
        &.slogan-text-2 {
          span {
            margin-right: 1.37em;
          }
        }
        &.slogan-text-3,
        &.slogan-text-4 {
          span {
            margin-right: 3.73em;
          }
        }
        &.slogan-text-5,
        &.slogan-text-6 {
          span {
            margin-right: 2.55em;
          }
        }
      }
    }
  }
}

.main-second {
  z-index: 10;
  @include w-center ($position: fixed, $w-percent: 50%);
  min-width: 1280px;
  height: 100vh;
  display: none;
  font-family: $font-family-ko;
  .second-text-wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    text-align: center;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    p {
      color: #fff;
      font-size: 30px;
      font-size: rem(30px);
      display: inline-block;
      opacity: 0;
      span {
        margin-right: 1.67em;
      }
      &.slogan-text-7 {
        font-weight: 250;
      }
      &.slogan-text-8 {
        font-weight: 500;
      }
      &.slogan-text-9 {
        font-weight: 300;
        span:last-child {
          margin-right: 0;
        }
      }
    }
  }
  .main-second-menu {
    position: absolute;
    top: 58%;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
    -moz-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    -o-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    &>ul>li {
      float: left;
      margin-right: 180px;
      opacity: 0;
      &:last-child {
        margin-right: 0;
      }
      &>a {
        font-weight: 250;
        font-size: 24px;
        font-size: rem(24px);
        color: #5afeb6;
        &:hover {
          font-style: oblique;
        }
      }
    }
    &>span {
      position: absolute;
      top: -200%;
      left: 200%;
      display: block;
      width: 200px;
      height: 1px;
      opacity: 0;
      background-color: #284853;
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
      transform: rotate(-45deg);
    }
  }
}

