.darum-logo {
  position: relative;
  width: 200px;
  height: 271px;
  margin: 0 auto;
  h2>p {
    position: absolute;
    width: 100%;
    font-weight: 900;
    font-size: 48px;
    font-size: rem(48px);
    color: #fff;
    letter-spacing: 20px;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    &.port-title {
      top: 30%;
      left: 55%;
      letter-spacing: 20px;
    }
    &.contact-title {
      top: 14%;
      left: 53%;
      line-height: 60px;
      letter-spacing: 10px;
    }
    &.inside-title {
      width: 150%;
      top: 8%;
      left: 57%;
      line-height: 75px;
      letter-spacing: 25px;
    }
  }
  .logo-green {
    path {
      fill: #5afeb6;
      fill-rule: evenodd;
    }
  }
  .logo-purple {
    path {
      fill: #6e3af6;
      fill-rule: evenodd;
    }
  }
  .logo-top {
    position: absolute;
    top: 0;
    left: 0;
  }
  .logo-bottom {
    position: absolute;
    top: 20px;
    left: 20px;
  }
  .logo-opacity {
    path {
      opacity: .7;
    }
  }
