.port-list-wrap {
  width: 1216px;
  margin: 0 auto;
  padding: 60px 0 100px 0;
  text-align: center;
}

.port-list {
  @extend %clearfix;
  &.second-list {
    display: none;
  }
  &>li {
    position: relative;
    width: 600px;
    height: 600px;
    float: left;
    margin: 0 16px 16px 0;
    &:nth-child(2n) {
      margin-right: 0;
    }
    &>a {
      display: none;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(110, 58, 246, .9);
      color: #fff;
      .port-cover-desc {
        width: 100%;
        font-weight: 250;
        font-size: 42px;
        font-size: rem(42px);
        line-height: 50px;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
      }
      .port-cover-title {
        font-weight: 300;
        font-size: 18px;
        font-size: rem(18px);
        position: absolute;
        top: 80%;
        left: 50%;
        width: 100%;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        transform: translateX(-50%);
      }
      .port-cover-sub {
        font-family: 'akrobatbold';
        font-size: 16px;
        font-size: rem(16px);
        position: absolute;
