input[type="checkbox"] {
  display: none;
  &+label {
    display: inline-block;
    width: 24%;
    line-height: 33px;
    font-weight: 300;
    font-size: 18px;
    font-size: rem(18px);
    color: #fff;
    padding: 4px 0 0 43px;
    margin-bottom: 10px;
    background: url('../images/pc/bg/bg-checkbox.png') 3px 8px no-repeat;
  }
  &:checked+label {
    font-weight: 700;
    background: url('../images/pc/bg/bg-checkbox-checked.png') left -1px no-repeat;
  }
  &.privacy-agree+label {
    width: auto;
    font-size: 14px;
    font-size: rem(14px);
    margin-left: -3px;
  }
  &.privacy-agree:checked+label {
    font-weight: 500;
    font-size: 14px;
    font-size: rem(14px);
  }
}

