// ======================================================================
// [Animation] Slide Up
// ======================================================================
@keyframes slideUp {
  0% {
    transform: translateY(0%); }
  50% {
    transform: translateY(-50%); }
  100% {
    transform: translateY(-100%); } }

@-webkit-keyframes slideUp {
  0% {
    -webkit-transform: translateY(0%); }
  50% {
    -webkit-transform: translateY(-50%); }
  100% {
    -webkit-transform: translateY(-100%); } }
