@mixin input-type ($w: 130px, $h: 35px, $p: 0 10px, $bdz: 1px, $bdc: #c4c4c4, $bd-focus: 1px solid #3466a2,  $bg: #fff) {
    min-width: $w;
    height: $h;
    input {
        width: $w;
        height: $h;
        line-height: $h - ($bdz * 2);
        padding: $p;
        border: $bdz solid $bdc;
        background: $bg;
        vertical-align: top;
    }
    label {
        line-height: $h;
        .hidden {
            @include a11y-invisible(false);
        }
    }
}
