// =======================================
// Font Settings
// =======================================

// rem 단위 계산을 위한 html 사이즈 리셋
$html-fz: 10px;

// 본문(paragraph) 사이즈
$body-fz: 14px;

// 기본 행간 비율
$line-height: 1.5;

// 본문 행간 비율
$base-line: $body-fz * 1.5;

// 적용 웹 폰트
$font-family-ko: "Noto Sans KR", sans-serif;
$font-family-en: "akrobatregular", Arial, sans-serif, serif;