// =======================================
// Font Language
// =======================================
html {
  font-size: $html-fz;
  body {
    line-height: $base-line; }
  &[lang="ko-KR"], &[lang="ko"] {
    font-family: $font-family-ko;
    body {
      font-size: $body-fz; } }
  &[lang="en"], *[lang="en"] {
    font-family: $font-family-en;
    body {
      font-size: $body-fz + 2px; } } }

// =======================================
// Typography
// =======================================
// Heading Retina Design Font Size 설정
$h2-fz: 26px;
$h3-fz: 23px;
$h4-fz: 20px;
$h5-fz: 18px;
$h6-fz: 16px;

// 제목 폰트 사이즈, 행간 비율 (px, rem)
h2 {
  @include psd-fz($h2-fz, 1);
  @include psd-lh($h2-fz, 1.5); }

h3 {
  @include psd-fz($h3-fz, 1);
  @include psd-lh($h3-fz, 1.5); }

h4 {
  @include psd-fz($h4-fz, 1);
  @include psd-lh($h4-fz, 1.5); }

h5 {
  @include psd-fz($h5-fz, 1);
  @include psd-lh($h5-fz, 1.5); }

h6 {
  @include psd-fz($h6-fz, 1);
  @include psd-lh($h6-fz, 1.5); }
