// ======================================================================
// [Breakpoint] Media Queries Variables
// ======================================================================

// 디바이스 좌,우 여백 간격
$grid-margin: 20px * 2;

// 모바일 디바이스 중단점
$mobile: 320px + $grid-margin;

// 테블릿 디바이스 중단점
$tablet: 720px + $grid-margin;

// 데스크탑 디바이스 중단점
$desktop: 1300px + $grid-margin;
